import styled from 'styled-components';


// Row Base
export const RowBaseContainer = styled.div`
  display: table;
  width: 100%;
`;

export const RowBase = styled.div`
  display: table-row;
`;

export const ColumnBase = styled.div`
  display: table-cell;
  border-bottom: 1px solid grey;
`;





